@charset "UTF-8";
input[type=text] {
  width: 100%;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 20px;
  background-color: white;
  padding: 10px 40px 10px 10px;
  transition: .4s; }
  input[type=text]:focus {
    border: 2px solid #333;
    outline: none; }
  input[type=text]:focus ~ .input-add-img {
    filter: invert(0); }

.input-container {
  margin: 0 auto;
  width: 80%;
  max-width: 500px;
  position: relative; }

.input-add-img {
  content: url("../image/add.svg");
  width: 20px;
  height: 20px;
  position: absolute;
  right: 15px;
  top: 13px;
  cursor: pointer;
  filter: invert(0.8);
  transition: filter .1s; }

.switch-container {
  margin: 15px auto; }
  .switch-container div {
    display: inline-block;
    border: 1px solid #222222;
    font-size: 16px;
    padding: 4px 9px;
    cursor: pointer;
    font-weight: bold;
    transition: .3s;
    user-select: none; }
    .switch-container div:hover {
      background: #B7B7B7; }
  .switch-container .switch-active {
    color: white;
    background: #222; }
    .switch-container .switch-active:hover {
      background: #222; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.todolist-grid-container > div:nth-child(1), .todolist-grid-container > div:nth-child(2), .todolist-grid-container > div:nth-child(3) {
  display: grid;
  justify-items: center;
  align-content: center; }

.todolist-grid-container {
  display: grid;
  font-size: 18px;
  width: 350px;
  min-height: 50px;
  grid-template-columns: 1fr 50px 50px;
  grid-template-areas: "text modify remove";
  opacity: 0;
  animation-name: fadeIn;
  animation-duration: 0.3s;
  animation-fill-mode: both;
  margin: 15px auto;
  border-bottom: 3px solid #333;
  transition: background .5s, border .5s; }
  .todolist-grid-container img {
    max-width: 20px;
    max-height: 20px;
    cursor: pointer;
    filter: invert(0);
    transition: filter 0.1s; }
    .todolist-grid-container img:hover {
      filter: invert(0.3); }
    .todolist-grid-container img:active {
      filter: invert(0.5); }
  .todolist-grid-container > div:nth-child(1) {
    justify-content: start;
    text-align: start;
    grid-area: text;
    padding: 10px; }
  .todolist-grid-container > div:nth-child(2) {
    grid-area: modify; }
  .todolist-grid-container > div:nth-child(3) {
    grid-area: remove; }

.icon-uncheck {
  content: url("../image/undo.svg"); }

.icon-check {
  content: url("../image/check.svg"); }

.icon-remove {
  content: url("../image/remove.svg");
  max-width: 14px;
  max-height: 14px; }

.todo-completed {
  background: #AAA;
  border-bottom: 3px solid #AAA;
  border-radius: 3px; }

.no-data {
  margin-top: 50px; }

html,
body,
#root {
  margin: 0px;
  padding: 0px;
  font-family: Arial, "文泉驛正黑", "WenQuanYi Zen Hei", "儷黑 Pro", "LiHei Pro", "微軟正黑體", "Microsoft JhengHei", "標楷體", "DFKai-SB", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  background: #dddddd;
  box-sizing: border-box;
  width: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.app {
  color: #333;
  width: 100%;
  margin: 100px auto auto auto;
  text-align: center; }

.link {
  position: fixed;
  top: 9px;
  right: 9px;
  height: 20px;
  width: 300px;
  font-size: 10px;
  text-align: end;
  cursor: pointer; }
  .link > div {
    font-size: 14px;
    display: inline-block;
    margin-right: 5px; }
    .link > div > a {
      position: relative;
      top: -4px;
      color: #777;
      font-weight: bold;
      text-decoration: none; }
  .link .github-icon {
    content: url("../image/github.svg");
    max-height: 20px;
    max-width: 20px;
    display: inline-block; }

.hide {
  display: none; }
